@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  transition: all 0.2s linear;
  font-family: Nunito, sans-serif;
}

body {
  background-color: rgb(255, 233, 207);
  
}

.App .header {
  background-color: rgb(80, 42, 118);
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 90px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
}

.App .header .logo h1 {
  font-size: 35px;
  color: chartreuse;
  font-weight: 800;
}

.App .header .logo h1:hover {
  cursor: pointer;
}

.App .userContainer {
  display: flex;
  /* margin-top: 50px; */
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.App .header .getDetails button {
  font-size: 20px;
  font-weight: 700;
  padding: 12px;
  border-radius: 15px;
  background-color: rgb(211, 211, 211);
}

.App .header .getDetails button:hover {
  background-color: rgb(237, 237, 237);
  cursor: pointer;
}

.App .splace {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.App .splace .preBox {
  height: 300px;
  width: 450px;
  background-color: rgb(121, 220, 190);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  box-shadow: 10px 10px 20px rgb(90, 90, 90);
}

.App .splace .preBox:hover {
  background-color: rgb(57, 220, 171);
  transition: 0.3s;
  cursor: pointer;
}

.userCard img {
  width: 100px;
  height: 100px;
  border-radius: 50px;
}

.userCard h1{
    color: rgb(189, 0, 252);
}

.userCard h2{
    color: rgb(177, 255, 180);
}

.userCard h1 span {
    color: rgb(58, 245, 58);
}

.userCard {
  display: flex;
  flex-wrap: wrap;
  width: 400px;
  height: 400px;
  background-color: aqua;
  margin: 20px;
  justify-content: center;
  flex-direction: column;
  align-content: center;
  line-height: 2;
  background: url("https://images.unsplash.com/photo-1529753253655-470be9a42781?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1534&q=80");
  animation: scroll 100s linear infinite;
  border-radius: 10px;
}

@keyframes scroll {
  100% {
    background-position: 0px -3000px;
  }
}

@media (prefers-reduced-motion) {
  .userCard {
    animation: scroll 200s linear infinite;
  }
}


